

const BaseHome=()=>import('@/view/BaseHome');
const AllCompitions=()=>import('@/view/AllCompitions');
const AllNews=()=>import('@/view/AllNews.vue');
const AllTeams=()=>import('@/view/AllTeams');
const AllVideo=()=>import('@/view/AllVideo');
const LivePlay=()=>import('@/view/LivePlay');
const LiveScores=()=>import('@/view/LiveScores');
const NotFound=()=>import('@/view/NotFound');
const BaseLogin=()=>import('@/view/BaseLogin');
const BaseJoin=()=>import('@/view/BaseJoin');
const BaseWorldcup=()=>import('@/view/BaseWorldcup');
const BaseLeagespain=()=>import('@/view/BaseLeagespain');
const PersianGulf=()=>import('@/view/PersianGulf');
const FranceLeag=()=>import('@/view/FranceLeag');
const ChampionLeage=()=>import('@/view/ChampionLeage');
const BoundesLeage=()=>import('@/view/BoundesLeage');
const EnglandLeage=()=>import('@/view/EnglandLeage');
const ItalyLeage=()=>import('@/view/ItalyLeage');
const TeamIran=()=>import('@/view/TeamIran');
const TeamEsteghlal=()=>import('@/view/TeamEsteghlal');
const TeamBayern=()=>import('@/components/main/TeamBayern/TeamBayern.vue')
const TeamPers=()=>import('@/components/main/TeamPerspolise/TeamPerspolise.vue')
const TeamMan=()=>import('@/components/main/TeamManchester/TeamManchester.vue')
const TeamReal=()=>import('@/components/main/TeamReal/TeamReal.vue')
const TeamLiverpol=()=>import('@/components/main/TeamLiver/TeamLiverpol')
const TeamBarcelona=()=>import('@/components/main/TeamBarc/TeamBarcelona')
const TeamYouventus=()=>import('@/components/main/TeamYouv/TeamYouventus')
import authentication from "@/middlewars/auth";

const routes=[

    {path:'/',name:'baseHome',component:BaseHome,meta:{title:'Home'}},
    {path: '/Competition',name:'AllCompetition',component:AllCompitions,meta:{title:'competition'},
        children:[
            {path:'/Competition/:postId(\\d+)/:slug',name:'worldcup',component:BaseWorldcup,meta:{title:'worldcup'}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'spainLeage',component:BaseLeagespain,meta:{title:'spainLeage'}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'persianGulf',component:PersianGulf,meta:{title:'persianGulf'}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'France',component:FranceLeag,meta:{title:'France'}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'champion',component:ChampionLeage,meta:{title:'champion',props:true}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'boundes',component:BoundesLeage,meta:{title:'BoundesLeage'}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'England',component:EnglandLeage,meta:{title:'England'}},
            {path:'/Competition/:postId(\\d+)/:slug',name:'Italy',component:ItalyLeage,meta:{title:'Italy'}},
        ]
    },
    {path: '/news/:slug',name:'AllNews',component:AllNews,meta:{title:'news'},},
    {path: '/teams',name:'AllTeams',component:AllTeams,meta:{title:'teams'},
        children: [
            {path:'/team/:postId(\\d+)/:slug',name:'iran',component:TeamIran,meta:{title:'iran'}},
            {path:'/team/:postId(\\d+)/:slug',name:'esteghlal',component:TeamEsteghlal,meta:{title:'Esteghlal'}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamBayern',component:TeamBayern,meta:{title:'Bayern'}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamPerspolise',component:TeamPers,meta:{title:'perspolise'}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamMan',component:TeamMan,meta:{title:'TeamMan'}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamReal',component:TeamReal,meta:{title:"TeamReal"}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamLiverpol',component:TeamLiverpol,meta:{title:'TeamLiver'}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamBarcelona',component:TeamBarcelona,meta:{title:'TeamBarc'}},
            {path:'/team/:postId(\\d+)/:slug',name:'TeamYouventus',component:TeamYouventus,meta:{title:"TeamYouv"}}
        ]
    },
    {path: '/video',name:'AllVideo',component:AllVideo,meta:{title:'video',requiresAuth:true,props:true},beforeEnter:[authentication]},
    {path: '/live',name:'LivePlay',component:LivePlay,meta:{title:'live',replace:true}},
    {path: '/404',name:'NotFound',component:NotFound,meta:{title:'notfound'}},
    {path: '/live-scores/:slug',name:'LiveScores',component:LiveScores,meta:{title:'scores'}},
    {path:'/login',name:'login',component:BaseLogin,meta: {title: 'login',guest:true}},
    {path:'/join',name:'join',component:BaseJoin,meta:{title: 'join'}},
    {path: '/:pathMatch(.*)*',redirect:'/404'},

]
export default routes;