
import{Form as VeeForm,Field as VeeField,defineRule,ErrorMessage,configure} from 'vee-validate';
import{required,min,max,alpha_spaces as alphaSpaces,email,min_value as minVal,max_value as maxVal,confirmed,not_one_of as excluded} from "@vee-validate/rules";


export default {
    install(app){
        app.component('VeeForm',VeeForm)
        app.component('VeeField',VeeField)
        app.component('ErrorMessage',ErrorMessage)

        defineRule('required',required)
        defineRule('check',required)
        defineRule('min',min)
        defineRule('max',max)
        defineRule('alpha_spaces',alphaSpaces)
        defineRule('email',email)
        defineRule('min_value',minVal)
        defineRule('max_value',maxVal)
        defineRule('password_match',confirmed)
        defineRule('excluded',excluded)
        defineRule('country_excluded',excluded)

        configure({
            validateOnBlur:true,
            validateOnInput:false,
            validateOnChange:true,
            validateOnModelUpdate:true,
            generateMessage:(context)=>{
                const messages={
                    required:`the field ${context.field} id required`,
                    min:`the field ${context.field} is the short`,
                    max:`the field ${context.field} is long`,
                    alpha_spaces:`the field ${context.field} to be character is spaces`,
                    email:`the field is ${context.field} is email`,
                    min_value:`the field is ${context.field} is short value`,
                    max_value:`the field is ${context.field} is max value`,
                    excluded:`you must not change ${context.field} is.`,
                    country_excluded:`we not accept this country`,
                    password_match:`the password is not match !`,
                    check:`you must accept is service`
                };
             const message=messages[context.rule.name] ? messages[context.rule.name] : `the field ${context.field}`;
             return message;
            }
        })


    }
}